.Projects{
    background-color: #e5e5e5;
    height: auto;
    width: 100%;
    display: inline-block;
    color:black;
    margin: 0;
    justify-content: center;
    align-items: center;
    text-align: center;
    /*overflow-y: auto;*/
    -webkit-clip-path: polygon(0 15%, 4% 3%, 14% 1%, 85% 0%, 96% 3%, 100% 15%, 100% 93%, 97% 98%, 84% 99%, 12% 100%, 3% 99%, 0 91%);
    clip-path: polygon(0 15%, 4% 3%, 14% 1%, 85% 0%, 96% 3%, 100% 15%, 100% 93%, 97% 98%, 84% 99%, 12% 100%, 3% 99%, 0 91%);
}
.Projects h2{
    margin-top: 5%;
    font-variant: small-caps;
    font-weight: lighter;
}
.Projects h3{
    font-variant: small-caps;
    font-weight: lighter;
    text-align: left;
    padding-left: 0 15%;
}
.Projects p{
    font-size: 20px;
    text-align: justify;
    padding:0 15%;
    
}
.Projects p a{
    text-decoration: none;
    color:#000;
}
@media only screen and (max-width: 600px) {
    .Projects h2{ 
        margin-top: 10%;
    }
    .Projects p, .Projects p a{
        font-size: 15px;
    }
}