body {
  margin: 0;
  padding: 0;
  font-family: 'Plus Jakarta Display', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

h1{
  font-weight: 900px;
  font-size: 45px;
}
h2{
  font-weight: 700px;
  font-size: 25px;
}
p{
  font-weight: 500px;
  font-size: 23px;
}
a{
  font-weight: 500px;
  font-size: 21px;
}