.About{
    background-color: #fca311;
    height: auto;
    width: 100%;
    display: inline-block;
    color:seashell;
    margin: 0;
    justify-content: center;
    align-items: center;
    /*overflow-y: auto;*/
    -webkit-clip-path: polygon(6% 1%, 100% 0%, 100% 100%, 0% 100%);
    clip-path: polygon(6% 1%, 100% 0%, 100% 100%, 0% 100%);
}
.About h2{ 
    text-align: center;
    margin-top: 5%;
    font-variant: small-caps;
    font-weight: lighter;
}
.About p{
    line-height: 2.1rem;
    font-size: 22px;
    text-align: justify;
    padding: 0px 10%;
}

@media only screen and (max-width: 600px) {
    .About h2{ 
        margin-top: 10%;
    }
    .About p{
        font-size: 15px;
    }
}