.Contact{
    background-color: #000001;
    height: auto;
    width: 100%;
    display: inline-block;
    color:seashell;
    margin: 0;
    justify-content: center;
    color:#fff;
    align-items: center;
    
    }
.Contact h2{ 
    text-align: center;
    margin-top: 5%;
    font-variant: small-caps;
    font-weight: lighter;
}
.Contact p{
    line-height: 2.1rem;
    font-size: 22px;
    text-align: center;
    margin: 5% 0;
}
.Contact p a{
    display: inline-block;
    text-decoration: none;
    color:#fff;
    margin: 0 8%;
}
@media only screen and (max-width: 600px) {
    .Contact h2{ 
        margin-top: 10%;
    }
    .Contact p a {
        font-size: 15px;
    }
}