.Home{
    background-color: #023047;
    height: 100vh;
    width: 100%;
    display:flex;
    color:seashell;
    margin: 0;
    justify-content: center;
    align-items: center;
    -webkit-clip-path: polygon(100% 0, 100% 65%, 76% 100%, 0 100%, 0 0);
    clip-path: polygon(100% 0, 100% 65%, 76% 100%, 0 100%, 0 0);
}
.Home .Container .Hello{
    font-variant: small-caps;
    font-weight: lighter;
    text-align: center;
}
.Home .Container h2{
    text-align: center;
    font-weight: lighter;
}
.Home .Container .Icons i
{
    display: inline-block;
    margin: 13%;
    cursor: pointer;
    font-size: 21px;
    color:grey;
}
.Home .Container .Icons i:hover{
    color: white;

}
.Home .Container img{
    transform: translate(35%,-10%);
    height:200px;
    width: 200px;
    border-radius:50% ;
}
@media only screen and (max-width: 600px) {
    .Home .Container .Hello{
        font-variant: small-caps;
        font-weight: lighter;
        font-size: 28px;
    }
    .Home .Container h2{
        font-weight: lighter;
        font-size: 24px;
    }
    .Home .Container img{
        transform: translate(33%,-5%);
    }
}
@media only screen and (min-width: 600px) {
    .Home .Container img{
        transform: translate(33%,10%);
    }
    .Home{
        height: auto;
    }
}
@media only screen and (max-height: 500px) {
    .Home{
        height: auto;
    }
    .Home .Container img{
        transform: translate(33%,11%);
    }
}
@media only screen and (min-width: 900px) {
    .Home .Container img{
        transform: translate(35%,-10%);
    }
    .Home{
        height: 100vh;
    }
}